import React, { useState } from "react";
import { ToastContainer, toast } from 'react-toastify';
import { Link, useNavigate } from 'react-router-dom';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import "../styles/register.scss";
import Navbar from "../components/Navbar";
import "react-toastify/dist/ReactToastify.css";
export const Register = () => {

    const [username, usernamechange] = useState("");
    const [fullname, fullnamechange] = useState("");
    const [email, emailchange] = useState("");
    const [phone, phonechange] = useState("");
    const [password, passwordchange] = useState("");
    const [repeatPassword, repeatPasswordchange] = useState("");

    const history = useNavigate();

    const isValidate = () => {
        let isprocceed = true;
        let errmessage = '';
        if (username === null || username === '') {
            isprocceed = false;
            errmessage = 'Vui lng nhp username';
            toast.warn(errmessage);
        }
        if (fullname === null || fullname === '') {
            isprocceed = false;
            errmessage = 'Vui lng nhp H tn';
            toast.warn(errmessage);
        }
        if (email === null || email === '') {
            isprocceed = false;
            errmessage = 'Vui lng nhp email';
            toast.warn(errmessage);
        }
        if (!email.includes("@")) {
            isprocceed = false;
            errmessage = 'Email khng hp l';
            toast.warn(errmessage);
        }
        if (phone.length != 10) {
            isprocceed = false;
            errmessage = 'S in thoi phi  10 s';
            toast.warn(errmessage);
        }
        if (password.length < 4) {
            isprocceed = false;
            errmessage = ' di mt khu phi ln hn 4';
            toast.warn(errmessage);
        }
        if (password !== repeatPassword) {
            isprocceed = false;
            errmessage = 'Mt khu khng trng khp';
            toast.warn(errmessage);
        }
        return isprocceed;
    }
    const handlesubmit = (e) => {
        e.preventDefault();
        let regisObj = { username, fullname, email, phone, password, repeatPassword };

        if (isValidate()) {
            fetch("https://travel-booking-1.onrender.com/api/users", {
                method: "POST",
                headers: { 'content-type': 'application/json' },
                body: JSON.stringify(regisObj)
            }).then((res) => {
                history('/login')
                localStorage.setItem("users", JSON.stringify(regisObj));
            }).catch((err) => {
                toast.error(err.message + 'failed');
            });
        }
    }
    return (
        <>
            <title>To ti khon</title>
            <Navbar />
            <div className="container1" onSubmit={handlesubmit}>
                <div className="wrapper">
                    <div className="title">To ti khon</div>
                    <Form className="formRegister">
                        <Form.Group className="inputValue" controlId="formBasicEmail">
                            <Form.Control value={username} type="text" name='username' onChange={e => usernamechange(e.target.value)} placeholder="Username" />
                        </Form.Group>
                        <Form.Group className="inputValue" controlId="formBasicEmail">
                            <Form.Control value={fullname} type="text" name='fullname' onChange={e => fullnamechange(e.target.value)} placeholder="H tn" />
                        </Form.Group>
                        <Form.Group className="inputValue" controlId="formBasicEmail">
                            <Form.Control value={email} type="email" name='email' onChange={e => emailchange(e.target.value)} placeholder="Email" />
                        </Form.Group>
                        <Form.Group className="inputValue" controlId="formBasicEmail">
                            <Form.Control value={phone} type="phone" name='phone' onChange={e => phonechange(e.target.value)} placeholder="S in thoi" />
                        </Form.Group>
                        <Form.Group className="inputValue" controlId="formBasicPassword">
                            <Form.Control value={password} type="password" name='password' onChange={e => passwordchange(e.target.value)} placeholder="Mt khu" />
                        </Form.Group>
                        <Form.Group className="inputValue" controlId="formBasicPassword">
                            <Form.Control value={repeatPassword} type="password" name='repeatPassword' onChange={e => repeatPasswordchange(e.target.value)}
                                placeholder="Nhp li mt khu" />
                        </Form.Group>
                        <span className="agreement">
                            Bng vic ng k, bn  ng  vi XYZ v
                            <span className="linkAgree"> iu khon s dng  </span> v
                            <span className="linkAgree"> Chnh sch bo mt</span>
                        </span>
                        <Button variant="primary" className='buttonRegis' type="submit">
                            To ti khon
                        </Button>
                    </Form>
                    <p className="mt-3" style={{ textAlign: "center" }}> Bn  c ti khon ?
                        <span> <Link to="/login">ng nhp</Link></span>
                    </p>
                </div>
                <ToastContainer />
            </div>
        </>
    )
}