import React, { useEffect, useState } from 'react'
import HomeCollectionTitle from './HomeCollectionTitle'
import '../styles/homeCollection.scss'
import ListCard from './ListCard'

export default function HomeCollection() {
  const [dataTourXanh, setDataTourXanh] = useState([])
  const [dataTourSinhThai, setDataTourSinhThai] = useState([])
  const [dataTourCongDong, setDataTourCongDong] = useState([])

  useEffect(() => {
    // Hm c gi khi component c render hoc state thay i
    fetchDataTourXanh();
    fetchDataTourSinhThai();
    fetchDataTourCongDong();
  }, []); // Tham s th hai l mt mng rng, ch gi useEffect mt ln sau khi component c mount

  async function fetchDataTourXanh() {
    try {
      const response = await fetch('https://travel-booking-1.onrender.com/api/tours?catagoryId=1&_limit=6'); // Gi API  ly d liu
      const jsonData = await response.json(); // Chuyn i d liu nhn c thnh JSON
      setDataTourXanh(jsonData); // Cp nht state vi d liu t API
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  }

  async function fetchDataTourSinhThai() {
    try {
      const response = await fetch('https://travel-booking-1.onrender.com/api/tours?catagoryId=3&_limit=6'); // Gi API  ly d liu
      const jsonData = await response.json(); // Chuyn i d liu nhn c thnh JSON
      setDataTourSinhThai(jsonData); // Cp nht state vi d liu t API
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  }

  async function fetchDataTourCongDong() {
    try {
      const response = await fetch('https://travel-booking-1.onrender.com/api/tours?catagoryId=2&_limit=6'); // Gi API  ly d liu
      const jsonData = await response.json(); // Chuyn i d liu nhn c thnh JSON
      setDataTourCongDong(jsonData); // Cp nht state vi d liu t API
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  }
  return (
    <div className='container home-collection'>
      <div className='row'>
        <div className='col-12'>
          <HomeCollectionTitle
            title={"im n du lch xanh yu thch"}
            strongText="Du lch xanh"
            des={"l mt hnh thc du lch bn vng, tp trung vo vic bo v mi trng, tn trng vn ha a phng v ti u ha li ch cho cng ng bn a. Hnh thc ny ngy cng c a chung khi vn  bo v mi trng v bin i kh hu tr thnh mi quan tm ton cu."}
          />
        </div>
        <div className='col-12'>
          <ListCard
            data={dataTourXanh} priceRow={true}
          />
        </div>
      </div>
      <div className='row'>
        <div className='col-12'>
          <HomeCollectionTitle
            title={"im n du lch cng ng yu thch"}
            strongText="Du lch cng ng"
            des={"l mt hnh thc du lch pht trin da trn s tham gia tch cc ca cng ng a phng, ni khch du lch c tri nghim cuc sng, vn ha, v phong tc tp qun bn a. Hnh thc ny khng ch mang li li ch kinh t m cn thc y bo tn vn ha v mi trng."}
          />
        </div>
        <div className='col-12'>
          <ListCard
            data={dataTourCongDong} priceRow={true}
          />
        </div>
      </div>
      <div className='row'>
        <div className='col-12'>
          <HomeCollectionTitle
            title={"im n du lch sinh thi yu thch"}
            strongText="Du lch sinh thi"
            des={`l hnh thc du lch da vo cc gi tr thin nhin, 
            gn vi bn sc vn ha a phng vi s tham gia ca cng ng nhm gip du khch c c 
            nhng tri nghim mi l ng thi gip mi ngi hiu c  thc v vic bo tn thin nhin v bo v mi trng.`}
          />
        </div>
        <div className='col-12'>
          <ListCard data={dataTourSinhThai} priceRow={true} />
        </div>
      </div>
    </div>
  )
}
