import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Apartment, EventNote, FlightTakeoff, QueryBuilder, } from "@material-ui/icons";
import { Link } from "react-router-dom";
import "../styles/cardItem.scss";
import { addCart, removeCart } from "../redux/slices/CartsSlice";
import { addCartTourToLocal, removeCartTourFromLocal } from "../utils/localStorageUtils";
import { formatPrice } from "../utils/utill";
import { toast } from 'react-toastify';

export default function CardItem(prop) {
    const [passengerCount, setPassengerCount] = useState(1);
    // State  lu tr s lng hnh khch, v hm  cp nht gi tr ca state

    let cartItems = useSelector((state) => state.carts);
    // S dng useSelector  ly gi tr ca state 'carts' t Redux store

    const dispatch = useDispatch();
    // S dng useDispatch  ly dispatch function t Redux store

    const [inCart, setInCart] = useState(false);
    // State  xc nh xem mt hng   c trong gi hng hay cha, v hm  cp nht gi tr ca state

    const handleAddToCart = () => {

        const cartItem = cartItems.find((item) => item.id === prop.idCard);
        // Kim tra xem mt hng  c trong gi hng cha bng cch tm kim trong mng cartItems

        if (cartItem) return;
        // Nu mt hng  tn ti trong gi hng, khng lm g c

        dispatch(addCart({ ...prop }));
        // Gi action 'addCart' vi thng tin mt hng v s lng hnh khch ti Redux store

        setInCart(true);
        // Cp nht trng thi inCart thnh true ( c trong gi hng)

        addCartTourToLocal(prop)
        toast.success(' thm vo gi hng', {
            position: "top-right",
            autoClose: 1000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            // transition: 'Bounce',
        });
    };

    const handleRemoveFromCart = () => {
        dispatch(removeCart(prop.idCard));
        // Gi action 'removeCart' vi idCard ca mt hng ti Redux store  xa mt hng khi gi hng

        setInCart(false);
        // Cp nht trng thi inCart thnh false (cha c trong gi hng)
        toast.info(' xo khi gi hng!', {
            position: "top-right",
            autoClose: 1000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            // transition: 'Bounce',
        });
        removeCartTourFromLocal(prop.idCard)
    };

    useEffect(() => {
        const isItemInCart = cartItems.some((item) => item.idCard === prop.idCard);
        // Kim tra xem mt hng c trong gi hng hay khng bng cch tm kim trong mng cartItems

        const storedItems = JSON.parse(localStorage.getItem("cart")) || [];
        // Ly d liu t localStorage (nu c) v chuyn i t chui JSON thnh mng

        const isItemStored = storedItems.some((item) => item.id === prop.idCard);
        // Kim tra xem mt hng c trong mng d liu t localStorage hay khng

        setInCart(isItemInCart || isItemStored);
        // Cp nht trng thi inCart da trn kt qu ca hai kim tra trn
    }, [cartItems, prop.idCard]);
    // Hm useEffect c gi mi khi cartItems hoc prop.idCard thay i,  kim tra xem mt hng c trong gi hng hay khng

    const priceRow = prop.priceRow || false
    return (
        <div className="col mt-4">

            <div className="card">
                <Link to={`/tourList/${prop.idCard}`}>
                    <img src={prop.image} style={{ minHeight: '197px' }} className="card-img-top" alt="..." />
                </Link>
                <div className="card-body">
                    <Link to={`/tourList/${prop.idCard}`}>
                        <h6 className="card-title mb-1 text-black">{prop.title}</h6>
                    </Link>
                    <div className={`d-flex ${priceRow ? 'flex-row' : 'flex-column'} justify-content-between`}>
                        <div className="d-flex flex-column">
                            <div className="time">
                                <QueryBuilder className="icon" />
                                <span>{prop.time}</span>
                            </div>
                            <div className="date">
                                <EventNote className="icon" />
                                <span>{prop.date}</span>
                            </div>
                            <div className="plan">
                                <FlightTakeoff className="icon" />
                                <span>{prop.plane}</span>
                            </div>
                            <div className="building">
                                <Apartment className="icon" />
                                <span>{prop.start} sao</span>
                            </div>
                        </div>
                        <div className="d-flex flex-column justify-content-between">
                            <span className="price text-right">
                                {formatPrice(prop.priceAdult)}
                            </span>

                        </div>
                    </div>
                    <div className="d-flex flex-center">
                        <button
                            className={`btn ${inCart ? "btn-danger remove-cart" : "add-cart"} rounded`}
                            onClick={inCart ? handleRemoveFromCart : handleAddToCart}
                        >
                            <i className="fas fa-cart-plus"></i>{" "}
                            {inCart ? "Xa khi gi" : "Thm vo gi"}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}