export const isEmailValid = (email) => {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
};
export const isPhoneValid = (phone) => {
  // Kim tra s in thoi c  10 s v s 0  u khng
  return phone.length === 10 && phone.startsWith("0");
};
export const isPassportNumberValid = (passportNumber) => {
  const passportNumberPattern = /^[A-Z]{1}[0-9]{7}$/; // Bc u t ch In hoa v tip theo l 7 ch s

  return passportNumberPattern.test(passportNumber);
};

export const formatCardNumber = (cardNumber) => {
  cardNumber = cardNumber + "";
  const formattedNumber = cardNumber.replace(/\s/g, "").replace(/(\d{4})/g, "$1 ");
  return formattedNumber.trim();
};
export const isNumericString = (str) => {
  const numericStr = str.replace(/\s/g, ''); // Loi b du khong trng
  return /^\d+$/.test(numericStr); // Kim tra chui s
};

export const now = () => {
  const currentDate = new Date();
  const day = String(currentDate.getDate()).padStart(2, '0');
  const month = String(currentDate.getMonth() + 1).padStart(2, '0');
  const year = currentDate.getFullYear();
  const formattedDate = `${day}/${month}/${year}`;

  return formattedDate;
};
export function formatDate(date) {
  const day = date.getDate();
  const month = date.getMonth() + 1;
  const year = date.getFullYear();
  return `${day}/${month}/${year}`;
};

export const ratingCore = (rating) => {
  switch (Math.ceil(rating)) {
    case 1:
      return 'T'
    case 2:
      return 'Khng tt'
    case 3:
    case 4:
    case 5:
      return 'Bnh thng'
    case 6:
    case 7:
    case 8:
      return 'Tt'
    case 9:
    case 10:
      return 'Rt tt'
    default:
      break;
  }
}