import React, {useEffect, useState} from "react";
import Swal from "sweetalert2";
import {isEmailValid, isPassportNumberValid, isPhoneValid} from "../../utils/utill";
import {useNavigate} from "react-router-dom";

export const FormInputInfoCustomer = ({cart, handleContinue}) => {
    const [customerInfo, setCustomerInfo] = useState([]);
    const quantity = cart.quantityAdult + cart.quantityChild;
    const navigate = useNavigate();
    let user = JSON.parse(localStorage.getItem("user"));
    const isCustomerInfoComplete = (index) => {
        const customer = customerInfo[index];
        // Kim tra cc trng cn nhp y 
        if (
            customer &&
            customer.full_name &&
            customer.email &&
            customer.phone &&
            customer.address
        ) {
            if (cart.tour.catagoryId === 2) {
                // Kim tra trng thm nu l catagoryId === 2
                if (customer.passport_number) {
                    return true; // Cc trng  c nhp y 
                }
            } else {
                return true; // Cc trng  c nhp y 
            }
        }

        return false; // C t nht mt trng cha c nhp y 
    };
    const handleSave = () => {
        // Kim tra thng tin khch hng
        const isComplete = isCustomerInfoComplete(0); // Kim tra khch hng u tin
        const isAuthenticated = user;
        if (!isAuthenticated) {
            Swal.fire({
                title: "Thng bo",
                text: "Vui lng ng nhp trc khi thanh ton",
                icon: "warning",
                confirmButtonText: "OK",
            });
            navigate('/login'); // Chuyn hng nu khng ng nhp
            return;
        }
        if (!isComplete) {
            // Hin th cnh bo nu thng tin cha y 
            Swal.fire({
                title: "Thng bo",
                text: "Vui lng nhp y  thng tin lin h!",
                icon: "error",
                confirmButtonText: "OK",
            });
            return; // Dng thc thi hm nu thng tin cha y 
        } else {
            const customer = customerInfo[0];
            const phone = customer.phone;
            if (!isPhoneValid(phone)) {
                // Hin th cnh bo nu s in thoi khng ng nh dng
                Swal.fire({
                    title: "Thng bo",
                    text: "S in thoi phi bt u bng s 0 v cha 10 s!",
                    icon: "error",
                    confirmButtonText: "OK",
                });
                return; // Dng thc thi hm nu s in thoi khng ng nh dng
            }
            const email = customer.email;
            if (!isEmailValid(email)) {
                // Hin th cnh bo nu email khng ng nh dng
                Swal.fire({
                    title: "Thng bo",
                    text: "Vui lng nhp ng nh dng email!",
                    icon: "error",
                    confirmButtonText: "OK",
                });
                return; // Dng thc thi hm nu email khng ng nh dng
            }
            if (customer.passport_number) {
                if (!isPassportNumberValid(customer.passport_number)) {
                    Swal.fire({
                        title: "Thng bo",
                        text: "S h chiu gm 8 k t, bt u bng 1 ch in hoa tip sau l 7 ch s!",
                        icon: "error",
                        confirmButtonText: "OK",
                    });
                    return;
                }
            }
        }

        // Thng tin  y , thc thi hm handleContinue
        handleContinue(customerInfo);
    };
    const handleInputChange = (index, field, value) => {
        setCustomerInfo((prevState) => {
            const updatedCustomerInfo = [...prevState];
            updatedCustomerInfo[index] = {
                ...updatedCustomerInfo[index],
                [field]: value
            };
            return updatedCustomerInfo;
        });
    };
    const [countries, setCountries] = useState([]);

    useEffect(() => {
        const fetchCountries = async () => {
            try {
                const response = await fetch('https://restcountries.com/v3.1/all');
                const data = await response.json();
                setCountries(data);
            } catch (error) {
                console.error('Error fetching countries:', error);
            }
        };

        fetchCountries();
    }, []);


    const forms = []

    for (let i = 0; i < quantity; i++) {
        let stt = 1 + i;
        forms.push(
            <React.Fragment key={i}>
                {
                    i ? (<>
                            <h3 key={i} className='mt-1'>Thng tin du khch #{stt}</h3>
                            <div className='row'>
                                <div className="col-md-6 mt-1">
                                    <div className="form-group">
                                        <label>Tn :</label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            id={`full_name-${i}`}
                                            placeholder="Nhp tn"
                                            onChange={(e) =>
                                                handleInputChange(i, "full_name", e.target.value)
                                            }
                                        /></div>
                                    <div className="form-group">
                                        <label>Email:</label>
                                        <input
                                            type="email"
                                            className="form-control"
                                            id={`email-${i}`}
                                            placeholder="abc@gmail.com"

                                            onChange={(e) => handleInputChange(i, "email", e.target.value)}
                                        />
                                    </div>

                                    {
                                        cart.tour.catagoryId === 2 ?
                                            <div className="form-group">
                                                <label>
                                                    S h chiu:
                                                </label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id={`passport_number-${i}`}
                                                    placeholder="A1234567"
                                                    maxLength={8}
                                                    onChange={(e) =>
                                                        handleInputChange(i, "passport_number", e.target.value)
                                                    }
                                                />
                                            </div>

                                            : ""
                                    }
                                </div>
                                <div className='col-md-6 mt-1'>
                                    <div className="form-group">
                                        <label>S in thoi:</label>
                                        <input
                                            type="number"
                                            className="form-control"
                                            id={`phone-${i}`}
                                            placeholder="Nhp s in thoi"
                                            onChange={(e) =>
                                                handleInputChange(i, "phone", e.target.value)
                                            }
                                        /></div>
                                    <div className="form-group">
                                        <label>a ch:</label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            id={`address-${i}`}
                                            placeholder="Nhp a ch"
                                            onChange={(e) =>
                                                handleInputChange(i, "address", e.target.value)
                                            }
                                        /></div>
                                    {
                                        cart.tour.catagoryId === 2 ?
                                            <div className="form-group">
                                                <label>Quc tch</label>
                                                <select className="custom-select"
                                                        onBlur={(e) => handleInputChange(i, "nationality", e.target.value)}>
                                                    {countries.map((country) => (
                                                        <option
                                                            key={country.name.common}
                                                            value={country.name.common}
                                                            selected={country.name.common === 'Vietnam'}
                                                        >
                                                            {country.name.common}
                                                        </option>
                                                    ))}
                                                </select>
                                            </div>
                                            : ""
                                    }
                                </div>
                            </div>
                        </>)
                        :
                        <>
                            <h3>Thng tin lin h:<small className="text-light"><i>bt buc(<span
                                className="text-danger">*</span>)</i></small></h3>
                            <div className="row ">
                                <div className="col-md-6 mt-1">
                                    <div className="form-group">
                                        <label>Tn (<span className="text-danger">*</span>):</label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            id={`full_name-${i}`}
                                            placeholder="Nhp tn"
                                            onChange={(e) =>
                                                handleInputChange(i, "full_name", e.target.value)
                                            }
                                        /></div>
                                    <div className="form-group">
                                        <label>Email(<span className="text-danger">*</span>):</label>
                                        <input
                                            type="email"
                                            className="form-control"
                                            id={`email-${i}`}
                                            placeholder="abc@gmail.com"
                                            onChange={(e) => handleInputChange(i, "email", e.target.value)}
                                        />
                                    </div>

                                    {
                                        cart.tour.catagoryId === 2 ?
                                            <div className="form-group">
                                                <label>
                                                    S h chiu(<span className="text-danger">*</span>){" "}
                                                </label>
                                                <input
                                                    type="text"
                                                    className="form-control"
                                                    id={`passport_number-${i}`}
                                                    maxLength={8}
                                                    placeholder="A1234567"
                                                    onChange={(e) =>
                                                        handleInputChange(i, "passport_number", e.target.value)
                                                    }
                                                />
                                            </div>

                                            : ""
                                    }
                                </div>
                                <div className='col-md-6 mt-1'>
                                    <div className="form-group">
                                        <label>S in thoi(<span className="text-danger">*</span>):</label>
                                        <input
                                            type="number"
                                            className="form-control"
                                            id={`phone-${i}`}
                                            placeholder="0987654321"
                                            onChange={(e) =>
                                                handleInputChange(i, "phone", e.target.value)
                                            }
                                        /></div>
                                    <div className="form-group">
                                        <label>a ch(<span className="text-danger">*</span>):</label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            id={`address-${i}`}
                                            placeholder="Nhp a ch"
                                            onChange={(e) =>
                                                handleInputChange(i, "address", e.target.value)
                                            }
                                        /></div>
                                    {
                                        cart.tour.catagoryId === 2 ?
                                            <div className="form-group">
                                                <label>Quc tch</label>
                                                <select className="custom-select"
                                                        onBlur={(e) => handleInputChange(i, "nationality", e.target.value)}>
                                                    {countries.map((country) => (
                                                        <option
                                                            key={country.name.common}
                                                            value={country.name.common}
                                                            selected={country.name.common === 'Vietnam'}
                                                        >
                                                            {country.name.common}
                                                        </option>
                                                    ))}
                                                </select>
                                            </div>
                                            : ""
                                    }
                                </div>
                            </div>

                        </>
                }
            </React.Fragment>
        );
    }
    return <>{forms}
        <div className="btn btn-block btn-continue-booking rounded" onClick={handleSave}>Tip tc</div>
    </>;
}