import React from "react";

export const Process = ({ step }) => {
    // Hm x l trng thi ca tng bc
    const getStepClass = (currentStep, targetStep) => {
        if (currentStep > targetStep) {
            return "step-done";
        } else if (currentStep === targetStep) {
            return "step-active";
        } else {
            return "step-todo";
        }
    };

    return (
        <div className="container">
            <div className="row">
                <div className="col-md-12">
                    <ol id="progress-bar">
                        <li className={getStepClass(step, 1)}>
                            <span className="step-title">ng nhp</span>
                        </li>
                        <li className={getStepClass(step, 2)}>
                            <span className="step-title">Chn tour</span>
                        </li>
                        <li className={getStepClass(step, 3)}>
                            <span className="step-title">Nhp thng tin</span>
                        </li>
                        <li className={getStepClass(step, 4)}>
                            <span className="step-title">Chn phng thc thanh ton</span>
                        </li>
                        <li className={getStepClass(step, 5)}>
                            <span className="step-title">Thanh ton</span>
                        </li>
                    </ol>
                </div>
            </div>
        </div>
    );
};
