import {useEffect, useState} from "react";
import {Process} from "../components/Process";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import {ItemTourCheckout} from "../components/checkout/ItemTourCheckout";
import {useDispatch, useSelector} from "react-redux";
import {removeCheckout, updateStatusPayment} from "../redux/slices/CheckoutSlice";
import {
    formatCardNumber,
    isNumericString,
    METHOD_1,
    METHOD_2,
    METHOD_3,
    METHOD_4
} from "../utils/utill";
import Swal from "sweetalert2";
import {addCheckoutToLocal, removeCartTourFromLocal, user} from "../utils/localStorageUtils";
import {clearSelectedTour} from "../redux/slices/SelectedTourSlice";
import {removeCart} from "../redux/slices/CartsSlice";
import {Link, useNavigate} from "react-router-dom";
import paymentImage from "../data/imgs/payment.png"
import cvcimage from "../data/imgs/cvc.png"

export const Checkout = () => {
    const user = JSON.parse(localStorage.getItem("user"));
    const navigate = useNavigate();
    const selectedTour = useSelector((state) => state.selectedTour);

    useEffect(() => {
        const isAuthenticated = user; // Kim tra trng thi ng nhp

        if (!isAuthenticated && !selectedTour) {
            navigate('/cart'); // Chuyn hng nu khng ng nhp v khng c selectedTour
        } else if (!isAuthenticated) {
            Swal.fire({
                title: "Thng bo",
                text: "Vui lng ng nhp trc khi thanh ton",
                icon: "warning",
                confirmButtonText: "OK",
            });
            dispatch(removeCheckout(checkout.id))
            navigate('/login'); // Chuyn hng nu khng ng nhp
            return;
        } else if (!selectedTour) {
            navigate('/cart'); // Chuyn hng nu khng c selectedTour
        }
    }, []);

    const [step, setStep] = useState(5);
    const [countries, setCountries] = useState([]);
    const [expiration, setExpiration] = useState('');
    const [cardNumber, setCardNumber] = useState('');
    const [cvc, setCVC] = useState('');
    const [country, setCountry] = useState('Vietnam');
    const dispatch = useDispatch();

    let checkout = useSelector((state) => state.checkout);
    checkout = checkout[checkout.length - 1] || {};
    let id = 0;
    switch (checkout.payment_method) {
        case METHOD_1:
            id = 1
            break;
        case METHOD_2:
            id = 2
            break;
        case METHOD_3:
            id = 3
            break;
        case METHOD_4:
            id = 4
            break;
        default:
            id = 0;
    }
    const handleConfirmCheckout = () => {
        let status;
        const isAuthenticated = JSON.parse(localStorage.getItem("user")); // Kim tra trng thi ng nhp

       if (!isAuthenticated) {
            Swal.fire({
                title: "Thng bo",
                text: "Vui lng ng nhp trc khi thanh ton",
                icon: "warning",
                confirmButtonText: "OK",
            });
            dispatch(removeCheckout(checkout.id))
            navigate('/login'); // Chuyn hng nu khng ng nhp
            return;
       }
        if (id === 0) {
            Swal.fire({
                title: "Thng bo",
                text: "Vui lng chn phng thc trc khi xc nhn thanh ton",
                icon: "warning",
                confirmButtonText: "OK",
            });
            return;
        }
        if (id === 1) status = "Ch thanh ton"
        if (id === 2) {
            if (cardNumber && expiration && cvc && country) {
                if (cardNumber.length !== 19) {
                    Swal.fire({
                        title: "Thng bo",
                        text: "S th tn dng phi  16 s!",
                        icon: "error",
                        confirmButtonText: "OK",
                    });
                    return;
                }
                if (cvc.length !== 3) {
                    Swal.fire({
                        title: "Thng bo",
                        text: "S CVC phi  3 s!",
                        icon: "error",
                        confirmButtonText: "OK",
                    });
                    return;
                }
                if (!isNumericString(cardNumber)) {
                    Swal.fire({
                        title: "Thng bo",
                        text: "S th tn dng phi l 1 dy s",
                        icon: "error",
                        confirmButtonText: "OK",
                    });
                    return;
                }
                status = " thanh ton"
            } else {
                Swal.fire({
                    title: "Thng bo",
                    text: "Vui lng nhp y  thng tin trc khi thanh ton!",
                    icon: "error",
                    confirmButtonText: "OK",
                });
                return;
            }
        }
        if (id === 3) status = "Ch thanh ton"
        if (id === 4) status = "Ch thanh ton"
        const newCheckout = {
            ...checkout,
            status: status
        }
        // cp nht trng thi ca ha n
        dispatch(updateStatusPayment(newCheckout))
        // thm vo local store
        addCheckoutToLocal(newCheckout)
        //xa sn phm khi gi hng trong localstorage
        removeCartTourFromLocal(checkout.tour.idCard)
        //xa tour c chn  thanh ton trong redux
        dispatch(clearSelectedTour())
        // xa sn phm khi gi hng redux
        dispatch(removeCart(checkout.tour.idCard))
        // xa ha n trong redux khi  lu trong localstorage
        dispatch(removeCheckout(checkout.id))
        // nh du hon thnh trn thanh process
        setStep(6)
        // gn li id;
        id = 0;
        Swal.fire({
            title: "Thng bo",
            text: "Xc nhn giao dch thnh cng!.Qu khch vui lng kim tra email  bit thm thng tin!",
            icon: "success",
            confirmButtonText: "OK",
        });
        return;
    }


    useEffect(() => {
        const fetchCountries = async () => {
            try {
                const response = await fetch('https://restcountries.com/v3.1/all');
                const data = await response.json();
                setCountries(data);
            } catch (error) {
                console.error('Error fetching countries:', error);
            }
        };

        fetchCountries();
    }, []);


    const handleCVCChange = (e) => {
        const value = e.target.value;
        const truncatedValue = value.slice(0, 3); // Gii hn  di 3 s
        setCVC(truncatedValue);
    };
    const handleExpirationChange = (e) => {
        const value = e.target.value;
        const truncatedValue = value.slice(0, 7); // Gii hn  di 7 k t
        setExpiration(truncatedValue);
    };

    return (
        <>
            <Navbar/>
            <Process step={step}/>
            <div className="bg-white">
            <div className="container pt-3 pb-3 mt-2 rounded" id="checkout">
                <div className="row">
                    <div className="col-md-8 mt-3">
                        {
                            id ?
                                id === 1 ? <div className="bg-light p-3">
                                        <p>Qu khch vui lng n trm ATM gn nht  thanh ton</p>
                                        <p>Thi gian ht hn thanh ton: Sau 3 ngy k t ngy xc nhn thanh ton!</p>
                                        <p>Sau khi thanh ton xong. Chng ti s gi mail xc nhn n bn!</p>
                                        <p>Chc bn c nhng khonh khc vui v khi tri nghim dch v ca chng ti!</p>
                                    </div>
                                    :
                                    id === 2 ?
                                        <>
                                            <div className="form-group">
                                                <label>Card number</label>
                                                <div className="input-group">
                                                    <input type="text" name="cart_number" className="form-control"
                                                           placeholder="1234 1234 1234 1234" maxLength={19}
                                                           value={formatCardNumber(cardNumber)}
                                                           onChange={(e) => setCardNumber(e.target.value)}
                                                    />
                                                    <div className="input-group-append">
                                                      <span className="input-group-text" style={{padding: 0}}>
                                                        <img
                                                            src={paymentImage}
                                                            alt="Icon"
                                                            style={{width: '150px', height: '20px', objectFit: 'cover'}}
                                                        />
                                                      </span>
                                                    </div>
                                                </div>
                                            </div>

                                            <div className="row">

                                                <div className="col-md-6">
                                                    <div className="form-group">
                                                        <label>Expiration </label>
                                                        <input type="month" name="expiration" className="form-control"
                                                               placeholder="MM/YY"
                                                               onChange={handleExpirationChange}
                                                        />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="form-group">
                                                        <label>CVC</label>
                                                        <div className="input-group">
                                                            <input type="number" name="cart_number"
                                                                   className="form-control"
                                                                   placeholder="CVC"
                                                                   value={cvc}
                                                                   onChange={handleCVCChange}
                                                            />
                                                            <div className="input-group-append">
                                                                <span className="input-group-text" style={{padding: 0}}>
                                                                  <img
                                                                      src={cvcimage}
                                                                      alt="Icon"
                                                                      style={{
                                                                          width: '30px',
                                                                          height: '20px',
                                                                          objectFit: 'cover'
                                                                      }}
                                                                  />
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>
                                            <label>Country</label>
                                            <select className="custom-select"
                                                    onBlur={(e) => setCountry(e.target.value)}>
                                                {countries.map((country) => (
                                                    <option key={country.name.common} value={country.name.common}
                                                            selected={country.name.common === 'Vietnam'}>
                                                        {country.name.common}
                                                    </option>
                                                ))}
                                            </select>
                                        </>
                                        : id === 3 ? <div className="bg-light p-3">
                                                <p>Qu khch vui lng chuyn khon n s ti khon ngn hng BIDV:<strong
                                                    className="border text-primary">3141 000 111 134</strong>  thanh ton</p>
                                                <p>Thi gian ht hn thanh ton: Sau 7 ngy k t ngy xc nhn thanh ton!</p>
                                                <p>Sau khi thanh ton xong. Chng ti s gi mail xc nhn n bn!</p>
                                                <p>Chc bn c nhng khonh khc vui v khi tri nghim dch v ca chng
                                                    ti!</p>
                                            </div> :
                                            id === 4 ?
                                                <div className="bg-light p-3">
                                                    <p>Qu khch vui lng n a ch:<strong
                                                        className="border text-primary">331 - ng s 17 - phng Linh Xun
                                                        - Tp Th c - Tp H Ch Minh</strong>  thanh ton</p>
                                                    <p>Thi gian ht hn thanh ton: Sau 7 ngy k t ngy xc nhn thanh
                                                        ton!</p>
                                                    <p>Sau khi thanh ton xong. Chng ti s gi mail xc nhn n bn!</p>
                                                    <p>Chc bn c nhng khonh khc vui v khi tri nghim dch v ca
                                                        chng ti!</p>
                                                </div>
                                                : ""
                                : ""
                        }
                        {
                            id ? <button
                                    className="btn btn-block btn-continue-booking rounded"
                                    onClick={handleConfirmCheckout}>
                                    Xc nhn
                                </button> :
                                ""
                        }
                    </div>
                    <div className="col-md-4 mt-3">
                        {
                            selectedTour ? <ItemTourCheckout selectedTour={selectedTour}/> :
                                ""
                        }
                    </div>

                </div>
                {!id ? (<>
                        <div className="row d-flex justify-content-center">
                            <div className="bg-light col-md-10 rounded mb-2 p-3">
                                <h3 className="text-center text-primary">Xc nhn thnh cng!</h3>
                                <p>Qu khch vui lng kim tra email  bit thm thng tin</p>
                                <p>Qu khch c bt k thc mc hoc vn  g hy lin h: <span className="text-primary">1900 1808</span>  c t vn</p>
                                <p>Rt vui khi c phc v qu khch</p>
                                <p>Xin cm n!</p>
                            </div>
                        </div>
                        <div className="row ">
                            <div className="col-md-12 d-flex justify-content-around flex-wrap">
                                <Link to={'/'}>
                                    <button
                                        className="btn btn-block btn-continue-booking rounded">
                                        Tr v trang ch
                                    </button>
                                </Link>
                                <Link to={'/cart'}>
                                    <button
                                        className="btn btn-block btn-amber btn-continue-booking rounded">
                                        Tip tc mua tour
                                    </button>
                                </Link>
                                <Link to={'/Booked'}>
                                    <button
                                        className="btn btn-block pr-5 pl-5 btn-continue-booking rounded">
                                        Tour  t
                                    </button>
                                </Link>
                            </div>
                        </div>

                    </>)
                    : ""
                }
            </div>
                </div>
            <Footer/>
        </>
    )
}