import {Link, useNavigate} from "react-router-dom";
import {CartItem} from "../components/CartItem";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import "../styles/cart.scss";
import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {removeCart, updateCart} from "../redux/slices/CartsSlice";
import {clearSelectedTour, setSelectedTour, updateSelectedTour} from "../redux/slices/SelectedTourSlice";
import Swal from "sweetalert2";
import {
    formatPrice, RATE_QUANTITY_OF_CHILD_WITH_ADULT,
} from "../utils/utill";
import {removeCartTourFromLocal, updateCartTourInLocal, user} from "../utils/localStorageUtils";

export const Cart = () => {
    const user = JSON.parse(localStorage.getItem("user"));

    // Ly danh sch cc tour trong gi hng t Redux store
    const cartItems = useSelector((state) => state.carts);

    // Ly thng tin v tour c chn t Redux store
    const selectedTour = useSelector((state) => state.selectedTour);


    const dispatch = useDispatch();

    // Trng thi khi to ca tour c chn (selectedItemId)
    const [selectedItemId, setSelectedItemId] = useState(selectedTour ? selectedTour.id : null);

    const navigate = useNavigate();

    // xa tour khi gi
    const handleDelete = (id) => {
        dispatch(removeCart(id)); // Gi action xa t Redux store

        // xa khi local
        removeCartTourFromLocal(id)

        // nu tour ny c trong tour  chn th xa 
        if (selectedTour && selectedTour.id === id) {
            dispatch(clearSelectedTour())
            setQuantityAdult(null);
            setQuantityChild(null);
        }
    };

    //tnh tng tin cc tour c trong gi
    const calculateTotal = () => {
        let total = 0;
        cartItems.forEach((item) => {
            if (item.tour.priceAdult) {
                const quantityChild = item.quantityChild !== null ? item.quantityChild : 0;
                const priceAdult = parseFloat(item.tour.priceAdult);
                const priceChild = parseFloat(item.tour.priceChild);
                total += priceAdult * item.quantityAdult + priceChild * quantityChild;
            }
        });
        // tr v nh dng tin VN
        return total
    };

    // tnh tng tin tour c chn
    const calculateTourSelected = () => {
        let total = 0;
        cartItems.forEach((item) => {
            if (item.id === selectedItemId) {
                const qChild = quantityChild !== null ? quantityChild : 0;
                const priceAdult = parseFloat(item.tour.priceAdult);
                const priceChild = parseFloat(item.tour.priceChild);
                total += priceAdult * quantityAdult + priceChild * qChild;
            }
        });
        // tr v nh dng tin VN
        return total
    };


    //  input nhp s lng hnh khch
    const [quantityAdult, setQuantityAdult] = useState(() => {
        const result = !selectedTour ? null : selectedTour.quantityAdult
        return result;
    })
    const [quantityChild, setQuantityChild] = useState(() => {
        const result = !selectedTour ? null : selectedTour.quantityChild
        return result;
    })

    const maxAdults = selectedItemId !== null ? cartItems.find((item) => item.id === selectedItemId)?.tour.quantity - quantityChild : 0;
    const maxChildren = selectedItemId !== null ? cartItems.find((item) => item.id === selectedItemId)?.tour.quantity - quantityAdult : 0;

    // Hm x l khi ngi dng kch chut vo  input khi cha c tick chn
    const handleInputFocus = () => {
        if (!selectedTour) {
            Swal.fire({
                title: "Thng bo",
                text: "Vui lng tick chn tour  thay i s lng!",
                icon: "info",
                confirmButtonText: "OK",
            });
        }
    };
    const handleAdultQuantityChange = (e) => {
        const value = parseInt(e.target.value);
        // nu s lng ngi ln m == null ( mc nh bng 1) th reset li child == null (mc nh bng 0)
        if (isNaN(value) || !selectedTour) {
            setQuantityAdult(null);
            setQuantityChild(null);
            return;
        }
        // nu gi tr nhp > s lng cho php th thng bo ng thi xt max gi tr
        else if (maxAdults !== null && value > maxAdults) {
            setQuantityAdult(maxAdults);
            Swal.fire({
                title: "Thng bo",
                text: "S lng khng c vt qu gii hn cho php.",
                icon: "warning",
                confirmButtonText: "OK",
            });
        }
            // nu sl ngi ln m nh hn t l quy nh
            // s chnh lch gia sl ngi ln v tr em (RATE_QUANTITY_OF_CHILD_WITH_ADULT)
        // th set li = tng sl ngi ln + chnh lch
        else if (value < quantityChild - RATE_QUANTITY_OF_CHILD_WITH_ADULT) {
            setQuantityChild(quantityAdult + RATE_QUANTITY_OF_CHILD_WITH_ADULT)
            Swal.fire({
                title: "Thng bo",
                text: `S tr em ch c ln hn ${RATE_QUANTITY_OF_CHILD_WITH_ADULT} ngi so vi ngi ln!`,
                icon: "warning",
                confirmButtonText: "OK",
            });
        }
        // nu nhp gi tr bng 0 th set v 1
        else if (value == 0) {
            setQuantityAdult(1);
            Swal.fire({
                title: "Thng bo",
                text: "S lng ngi ln phi t 1 tr ln.",
                icon: "warning",
                confirmButtonText: "OK",
            });
        } else {
            //trng hp hp l
            setQuantityAdult(value);
        }
    };

    const handleChildQuantityChange = (e) => {
        const value = parseInt(e.target.value);
        if (isNaN(value) || !selectedTour) {
            setQuantityChild(null)
            return
        }
            // nu gi tr nhp nh hn or bng mc qui nh v gi tr ny phi < hn or =
        // mc chnh lch sl ngi ln v tr em th ly gi tr ny
        else if (value <= maxChildren && value <= quantityAdult + RATE_QUANTITY_OF_CHILD_WITH_ADULT) {
            setQuantityChild(value);
        }
            // nu ln hn mc chnh lch th thng bo v set li gi tr bng s lng ch cn li
        // hoc cng c th l ng theo t l
        else if (value > quantityAdult + RATE_QUANTITY_OF_CHILD_WITH_ADULT) {
            setQuantityChild(quantityAdult + RATE_QUANTITY_OF_CHILD_WITH_ADULT > maxChildren ? maxChildren : quantityAdult + RATE_QUANTITY_OF_CHILD_WITH_ADULT)
            Swal.fire({
                title: "Thng bo",
                text: `S tr em ch c ln hn ${RATE_QUANTITY_OF_CHILD_WITH_ADULT} ngi so vi ngi ln!`,
                icon: "warning",
                confirmButtonText: "OK",
            });
        }
        // ngc li th set gi tr ln nht ca sl tr em c th c
        else {
            setQuantityChild(maxChildren)
            Swal.fire({
                title: "Thng bo",
                text: "S lng khng c vt qu gii hn cho php.",
                icon: "warning",
                confirmButtonText: "OK",
            });
        }
    };
    // hm x l thay i s lng hnh khch
    const handleQuantityChange = (itemId, quantityAdult, quantityChild) => {
        // cp nht tour trong cart redux
        dispatch(updateCart({
            idCard: itemId,
            quantityAdult: quantityAdult,
            quantityChild: quantityChild,
            total_price: calculateTourSelected()
        }));
        // cp nht tour c chn
        dispatch(updateSelectedTour({
            quantityAdult: quantityAdult,
            quantityChild: quantityChild,
            total_price: calculateTourSelected()}));
        updateCartTourInLocal(itemId, quantityAdult, quantityChild, calculateTourSelected())
    };


    // hm x l tour c chn  tin hnh thanh ton
    const handleSelection = (itemId) => {
        const selectedTour = cartItems.find((item) => item.id === itemId);
        // nu c tour c chn th set li  input vi s lng ngi ln v tr em trong tour 
        if (selectedTour) {
            setQuantityChild(selectedTour.quantityChild);
            setQuantityAdult(selectedTour.quantityAdult);
        } else {
            setQuantityChild(null);
            setQuantityAdult(null);
        }
        // nu khng c chn th clear Selectour i
        if (itemId === null) {
            dispatch(clearSelectedTour());
        }
        // chn li tour ny vo redux
        setSelectedItemId(itemId);
        dispatch(setSelectedTour(cartItems.find((item) => item.id === itemId)));
    };

    // gi hm handleQuantityChange khi 1 trong 2  nhp thay i s lng
    useEffect(() => {
        if (selectedItemId && (quantityAdult !== null || quantityChild !== null)) {
            handleQuantityChange(selectedItemId, quantityAdult, quantityChild);
        }
    }, [selectedItemId, quantityAdult, quantityChild]);

    const handCheckIsLogin = ()=> {
        const isAuthenticated = user; // Kim tra trng thi ng nhp
        if(!selectedTour){
            Swal.fire({
                title: "Thng bo",
                text: "Vui lng tick chn tour m bn cn thanh ton!",
                icon: "success",
                confirmButtonText: "OK",
            });
            return;
        }
        else if (!isAuthenticated) {
            Swal.fire({
                title: "Thng bo",
                text: "Vui lng ng nhp trc khi thanh ton",
                icon: "warning",
                confirmButtonText: "OK",
            });
            navigate('/login'); // Chuyn hng nu khng ng nhp
            return;
        } else navigate('/booking')
    }
    return (
        <>
            <Navbar/>
            <div className="bg-white">
            <div className="container ">
                <div className="container mt-5 p-3 rounded cart">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="d-flex flex-row align-items-center">
                                <i className="fas fa-arrow-left"></i>
                                <Link to={'/'}>
                                <span className="ml-2">Tip tc thm</span>
                                </Link>
                            </div>
                            <hr/>
                        </div>
                    </div>
                    <div className="row no-gutters">
                        <div className="col-md-8">
                            <div className="product-details mr-2">
                                <h4 className="mb-0 mb-2 item-title-cart">Gi hng</h4>
                                <div className={`overflow-auto overflow-cart scrollbar ${ cartItems.length > 0 ?'':'d-flex justify-content-center align-items-center'}`}>
                                    {cartItems.length > 0 ? (
                                        cartItems.map((item) => (
                                            <CartItem
                                                item={item}
                                                handleDelete={handleDelete}
                                                selectedItemId={selectedItemId}
                                                handleSelection={handleSelection}
                                                key={item.id}
                                            />
                                        ))
                                    ) : (
                                        <h5 className="d-flex justify-content-center align-items-center"><i>Cha c g trong gi!</i></h5>
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="col-md-4 mt-2">
                            <h4 className="mb-0 mb-2 item-title-cart">Chi tit</h4>
                            <div className="total-info">
                                <div className="d-flex justify-content-between information">
                                    <input
                                        type="number"
                                        className="form-control"
                                        id="name"
                                        placeholder={`${selectedTour !== null ? cartItems.find((item) => item.id === selectedTour.id)?.tour.priceAdult : ""} x s ngi ln`}
                                        inputMode="numeric"
                                        min="1"
                                        max={maxAdults}
                                        value={quantityAdult === null ? "" : quantityAdult}
                                        onMouseDown={handleInputFocus}
                                        onChange={handleAdultQuantityChange}
                                    />
                                </div>
                                <div className="d-flex justify-content-between information">
                                    <input
                                        type="number"
                                        className="form-control"
                                        id="name"
                                        inputMode="numeric"
                                        placeholder={`${selectedTour !== null ? cartItems.find((item) => item.id === selectedTour.id)?.tour.priceChild : ""} x s tr em t 2 n 7 tui`}
                                        min="0"
                                        max={maxChildren}
                                        value={quantityChild === null ? "" : quantityChild}
                                        onMouseDown={handleInputFocus}
                                        onChange={handleChildQuantityChange}
                                    />
                                </div>

                                <div className="d-flex justify-content-between information">
                                    <span>Tng tin gi hng:</span>
                                    <span>{formatPrice(calculateTotal())}</span>
                                </div>
                                {selectedTour ? (<>
                                    <div className="d-flex justify-content-between information">
                                        <span>Gi v cho ngi ln:</span>
                                        <span>{formatPrice(selectedTour.tour.priceAdult)}</span>
                                    </div>
                                    <div className="d-flex justify-content-between information">
                                        <span>Gi v cho tr em:</span>
                                        <span>{formatPrice(selectedTour.tour.priceChild)}</span>
                                    </div>
                                    <div className="d-flex justify-content-between information">
                                        <span>Tng tin tour c chn:</span>
                                        <span>{formatPrice(calculateTourSelected())}</span>
                                    </div>
                                </>) : ""}
                                    <button
                                        className="btn btn-begin-checkout btn-block d-flex justify-content-center mt-3 rounded"
                                        type="button"
                                        onClick={handCheckIsLogin}
                                    >
                                        <span>
                                          Tin hnh thanh ton <i className="fas fa-arrow-right ml-1"></i>
                                        </span>
                                    </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
            <Footer/>
        </>);
};